.introductionWrapper{
    display: grid;
    grid-template-columns: 1.5fr 4fr;
    grid-template-rows: 50%  auto;
    margin-top: -1px;
    background: linear-gradient(to top, #757f9a,#d7dde8);
    height: 90vh;
    overflow-x: hidden;
}
.contact a  {
    color: white;
  }

.gridItemsImg{
    grid-row-start: 1;
    grid-row-end: 3;
    place-self: center;
}


.gridItemsWhoAmI{
    
    grid-column-start: 2;
    grid-column-end: 3;
    place-self: start;
    overflow-wrap: break-word;
}

.introduction{
    display: grid;
    grid-template-columns: 1fr 1fr;
    place-self: center;
}

.info{
    place-self: center;
    text-align: center;
}


.desg{
    font-weight: 500;
    font-size: 15px;
    overflow-wrap: break-word;
}

.name{
    font-size: 50px;
    font-weight: bold;
    text-shadow: 2px 4px 20px gray;
    font-family: 'Sriracha',cursive;
}

.contact{
    margin: auto;
    padding:15px;
    width:70%;
    text-align: left;
    box-shadow: 8px 8px 11px 1px rgb(81,96,116);
    box-sizing: border-box;
    border-bottom: 1px inset black;
    overflow-wrap: break-word;
    border-radius: 5px;
    color: white;
    background: #616161;
}

@media(max-width:850px){

    .introductionWrapper{
      height:100% ;
      min-height: 100%;
      width: 100%;
      min-width: 100%; 
     
    }

    .gridItemsImg img{
        margin-left:-15px ;
    }
    .gridItemsWhoAmI p{
        box-sizing: border-box;
       
        width: 80%;
    }
    .name{
        font-size: 30px;
    }
    
    .gridItemsImg{
        margin-left: -15px;
        grid-row-start: 1;
        grid-row-end: 2;
        place-self: center;
    }
    .gridItemsWhoAmI{
     grid-column-start: 1;
      grid-column-end: 3;
      place-self: start;
    }    

    .desg{
        font-size: 14px;
    }
    .introduction{
        grid-template-columns: 1fr;
        grid-template-rows: 50% auto;
        place-self: center;
        margin-left: -10px;
    }

    .info{
        margin-left: -15px;
    }

}

.title{

    font-weight: bold;
    font-size: 30px;
    box-sizing: border-box;
    padding:5px;
    font-family: 'Sriracha',cursive;
    margin: 10px;
    animation: bounce 8s infinite;
}

@keyframes bounce{

    0%{
        transform: translateY(-70px);
    }
    4%{
        transform: translateY(0px);
    }
    8%{
        transform: translateY(-30px);
    }
    12%{
        transform: translateY(0px);
    }
    16%{
        transform: translateY(-16px);
    }
    20%{
        transform: translateY(0px);
    }

    100%{
        transform: translateY(0px);
    }
}

p{
    font-family: 'Caveat',cursive;
    font-size: 22px;
}